import React from 'react';
import PropTypes from 'prop-types';
import { Grid } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import SimpleTextField from 'components/Account/SimpleTextField';

class SecondPage extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      errors: {}
    };

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  validate() {
    const { name, surname } = this.props;

    const errors = {};
    if (name.length < 1) {
      errors.name = 'Tuias laukas negali bti paliktas.';
    }

    if (surname.length < 1) {
      errors.surname = 'Tuias laukas negali bti paliktas.';
    }

    this.setState({ errors });

    return Object.keys(errors).length === 0;
  }

  handleSubmit(e) {
    const { onSubmit } = this.props;

    return this.validate() ? onSubmit(e) : () => {};
  }

  render() {
    const { onChange, name, surname, previousPage } = this.props;
    const { errors } = this.state;

    return (
      <div>
        <SimpleTextField
          onChange={onChange}
          label="Vardas"
          error={errors.name}
          name="name"
          value={name}
        />
        <SimpleTextField
          onChange={onChange}
          error={errors.surname}
          label="Pavard"
          name="surname"
          value={surname}
        />
        <Grid container spacing={2} justify="center" style={{ marginTop: '15px' }}>
          <Grid item md={6}>
            <Button
              type="button"
              onClick={previousPage}
              variant="contained"
              style={{ width: '100%' }}
            >
              Atgal
            </Button>
          </Grid>
          <Grid item md={6}>
            <Button
              onClick={this.handleSubmit}
              variant="contained"
              color="primary"
              style={{ width: '100%' }}
            >
              Registruotis
            </Button>
          </Grid>
        </Grid>
      </div>
    );
  }
}

SecondPage.propTypes = {
  onChange: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
  previousPage: PropTypes.func.isRequired,
  name: PropTypes.string.isRequired,
  surname: PropTypes.string.isRequired
};

export default SecondPage;
